.ItemHeader {
  width: 100%;
  background-image: url("./ItemHeader-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: -1; }
  .ItemHeader:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 236, 0, 0.8); }
  .ItemHeader-Container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 30px 0; }
  .ItemHeader-Title {
    margin: 0;
    text-transform: uppercase;
    font-size: 30px;
    line-height: 30px;
    margin-bottom: 20px;
    position: relative;
    z-index: 1; }
  .ItemHeader-Text {
    font-size: 14px;
    line-height: 18px;
    position: relative;
    z-index: 1; }



