@media screen and (max-width: 500px) {
  .touch-available .CatalogRightSection {
    padding-right: 0; }
  .touch-available .CatalogRightSection-Button {
    margin-right: 6px;
    margin-left: 20px; }
    .touch-available .CatalogRightSection-Button:after {
      width: 15px;
      background-size: 15px;
      left: -20px; }
  .touch-available .CatalogRightSection-Text {
    font-size: 12px;
    line-height: 12px; } }



