.Catalog {
  width: 100%;
  border-top: 3px solid #ededed;
  border-bottom: 2px solid #ededed; }
  .Catalog-Container {
    max-width: 1200px;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    position: relative; }
  .Catalog-CatalogButton {
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 600;
    padding: 20px 40px 20px 20px;
    cursor: pointer;
    position: relative;
    background-color: #ededed; }
  .Catalog-CatalogButton::after {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -3px;
    right: 20px;
    border: 6px solid black;
    border-right-color: transparent;
    border-left-color: transparent;
    border-bottom-color: transparent;
    border-radius: 2px; }
  .Catalog-SearchSection {
    width: 40%;
    position: relative; }
  .Catalog-SearchButton {
    position: absolute;
    right: 2px;
    top: 2px;
    width: 35px;
    height: calc(100% - 4px);
    background-image: url("./Catalog-search.png");
    background-position: center;
    background-repeat: no-repeat;
    background-color: #ededed;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    cursor: pointer; }
  .Catalog-SearchButton:hover {
    background-color: #ffec00; }
  .Catalog-SearchButton:hover:after {
    border-right-color: #ffec00; }
  .Catalog-SearchButton:after {
    content: "";
    position: absolute;
    border: 6px solid #ededed;
    border-top-color: transparent;
    border-left-color: transparent;
    border-bottom-color: transparent;
    left: -12px;
    top: 50%;
    margin-top: -6px;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out; }
  .Catalog-Search {
    border: 2px solid #ededed;
    padding: 10px 35px 10px 10px;
    outline: none;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    height: 32px;
    width: 100%;
    position: relative; }
  .Catalog-Search::-webkit-input-placeholder {
    color: #c4c4c4; }
  .Catalog-Search::-ms-input-placeholder {
    color: #c4c4c4; }
  .Catalog-Search::placeholder {
    color: #c4c4c4; }
  .Catalog-Search:focus {
    border-color: #ffec00; }
  .Catalog-MenuSection {
    display: block;
    width: 100%; }
  .Catalog-Menu {
    list-style: none;
    margin: 0;
    padding: 0; }



