.ItemMenu {
  max-width: 1200px;
  height: 50px;
  margin: 0 auto; }
  .ItemMenu-Menu {
    list-style: none;
    padding: 0;
    margin: 0; }
  .ItemMenu-Link {
    display: inline-block;
    color: #6e6e6e;
    font-size: 12px;
    cursor: pointer;
    line-height: 50px;
    padding-right: 20px;
    position: relative;
    float: left; }
  .ItemMenu-Link:after {
    content: "";
    position: absolute;
    right: 0;
    width: 20px;
    height: 100%;
    background-image: url("./ItemMenu-arrow.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 10px; }
  .ItemMenu-Link_current {
    color: #cccccc;
    padding-right: 0; }
  .ItemMenu-Link_current:after {
    display: none; }



