    top: 50%;
    margin-top: -7px;
    right: 20px;
    border: 6px solid white;
    border-top-color: transparent;
    border-left-color: transparent;
    border-bottom-color: transparent;
    border-radius: 2px;
    bottom: unset;
    left: unset; }
  .touch-available .CatalogMenu-Link_type_parent.CatalogMenu-Link_mobile {
    margin-left: 0; }
    .touch-available .CatalogMenu-Link_type_parent.CatalogMenu-Link_mobile::after {
      right: 15px; }

.touch-available .CatalogMenu-Content {
  padding: 30px 20px;
  display: -ms-flexbox;
  display: flex;
  overflow-x: auto; }

.touch-available .CatalogMenu-Card_type_footer {
  margin-bottom: 20px;
  width: 100%; }

.touch-available .CatalogMenu-Card_type_footer:last-child {
  margin-bottom: 0; }



